export default {
	data() {
		return {
			content: '',
			action: '',
			maxSize: 2 * 1024 * 1024, //限制文件大小 2M
			btnLoading: false //防止重复点击
		};
	},
	methods: {
		oversizeUpload() {},
		//自定义标题栏按钮 h5&&app
		onNavigationBarButtonTap({ index }) {
			if (index == 0) {
				this.handleRelease()
			}
		},
		//自定义标题栏按钮点击事件  微信小程序
		handleRelease(){
			if (this.$u.trim(this.content) || this.$refs.upload.lists.length) {
				this.btnLoading = true;
				uni.showLoading({
					title: '正在发布...',
					mask: true
				});
				const { id, userName, headImg } = this._user_info;
				this.circleData.unshift({
					circleMegId: Date.now(),
					userId: id,
					userName,
					createTime: '刚刚',
					userHeadImg: headImg,
					content: this.content,
					imageList: this.$refs.upload.lists.map(it => it.url),
					isPraise: false,
					praise: [],
					comment: []
				});
				setTimeout(() => {
					this.btnLoading = false;
					uni.hideLoading();
					this.$u.route({ type: 'back'});
				}, 500);
			}
		}
	}
	
};
